import { useEffect, useMemo, useState, useCallback } from 'react';
import * as anchor from '@project-serum/anchor';

import styled from 'styled-components';
import { Container, Snackbar } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import Alert from '@material-ui/lab/Alert';
import { PublicKey } from '@solana/web3.js';
import { useWallet } from '@solana/wallet-adapter-react';
import { WalletDialogButton } from '@solana/wallet-adapter-material-ui';
import {
  awaitTransactionSignatureConfirmation,
  CandyMachineAccount,
  CANDY_MACHINE_PROGRAM,
  getCandyMachineState,
  mintOneToken,
} from './candy-machine';
import { AlertState } from './utils';
import { Header } from './Header';
import { MintButton } from './MintButton';
import { GatewayProvider } from '@civic/solana-gateway-react';
import Countdown from "react-countdown";
import { env } from 'process';
import { collapseTextChangeRangesAcrossMultipleVersions } from 'typescript';

const ConnectButton = styled(WalletDialogButton)`
  width: 100%;
  height: 60px;
  margin-top: 10px;
  margin-bottom: 5px;
  background-color: #1cc964;
  color: white;
  font-size: 16px;
  font-weight: bold;
`;

const MintContainer = styled.div``; // add your owns styles here

const renderCounter = ({days, hours, minutes, seconds, completed}: any) => {
  return (
    (completed ? null : <div id='countdown' className='countdown-time'>
        <div className='cd-block'><span className='cd-number'> {days} </span> <br/> <span
          className="cd-unit"> days </span></div>
        <span className='cd-sep'>:</span>
        <div className='cd-block'><span className='cd-number'> {hours} </span> <br/> <span
          className="cd-unit"> hours </span></div>
        <span className='cd-sep'>:</span>
        <div className='cd-block'><span className='cd-number'> {minutes} </span> <br/> <span
          className="cd-unit"> minutes </span></div>
        <span className='cd-sep'>:</span>
        <div className='cd-block'><span className='cd-number'> {seconds} </span> <br/> <span
          className="cd-unit"> seconds </span></div>
      </div>
    )
  );
};

export const toDate = (value?: anchor.BN) => {
  if (!value) {
    return;
  }
  return value.toNumber()*1000;
};

export interface HomeProps {
  candyMachineId?: anchor.web3.PublicKey;
  connection: anchor.web3.Connection;
  startDate: number;
  txTimeout: number;
  rpcHost: string;
}

const Home = (props: HomeProps) => {
  const [isUserMinting, setIsUserMinting] = useState(false);
  const [candyMachine, setCandyMachine] = useState<CandyMachineAccount>();
  const [alertState, setAlertState] = useState<AlertState>({
    open: false,
    message: '',
    severity: undefined,
  });

  const rpcUrl = props.rpcHost;
  const wallet = useWallet();

  const anchorWallet = useMemo(() => {
    if (
      !wallet ||
      !wallet.publicKey ||
      !wallet.signAllTransactions ||
      !wallet.signTransaction
    ) {
      return;
    }

    return {
      publicKey: wallet.publicKey,
      signAllTransactions: wallet.signAllTransactions,
      signTransaction: wallet.signTransaction,
    } as anchor.Wallet;
  }, [wallet]);

  const refreshCandyMachineState = useCallback(async () => {
    console.log("refresh");
    if (!anchorWallet) {
      return;
    }

    if (props.candyMachineId) {
      try {
        const cndy = await getCandyMachineState(
          anchorWallet,
          props.candyMachineId,
          props.connection,
        );
        setCandyMachine(cndy);
      } catch (e) {
        console.log('There was a problem fetching Candy Machine state');
        console.log(e);
      }
    }
  }, [anchorWallet, props.candyMachineId, props.connection]);

  const onMint = async () => {
    try {
      setIsUserMinting(true);
      document.getElementById('#identity')?.click();
      if (wallet.connected && candyMachine?.program && wallet.publicKey) {
        const mintTxId = (
          await mintOneToken(candyMachine, wallet.publicKey)
        )[0];

        let status: any = { err: true };
        if (mintTxId) {
          status = await awaitTransactionSignatureConfirmation(
            mintTxId,
            props.txTimeout,
            props.connection,
            true,
          );
        }

        if (status && !status.err) {
          setAlertState({
            open: true,
            message: 'Congratulations! Mint succeeded!',
            severity: 'success',
          });
        } else {
          setAlertState({
            open: true,
            message: 'Mint failed! Please try again!',
            severity: 'error',
          });
        }
        setTimeout(refreshCandyMachineState, 2000);
      }
    } catch (error: any) {
      let message = error.msg || 'Minting failed! Please try again!';
      if (!error.msg) {
        if (!error.message) {
          message = 'Transaction Timeout! Please try again.';
        } else if (error.message.indexOf('0x137')) {
          message = `SOLD OUT!`;
        } else if (error.message.indexOf('0x135')) {
          message = `Insufficient funds to mint. Please fund your wallet.`;
        }
      } else {
        if (error.code === 311) {
          message = `SOLD OUT!`;
          window.location.reload();
        } else if (error.code === 312) {
          message = `Minting period hasn't started yet.`;
        }
      }

      setAlertState({
        open: true,
        message,
        severity: 'error',
      });
    } finally {
      setIsUserMinting(false);
    }
  };

  useEffect(() => {
    refreshCandyMachineState();
  }, [
    anchorWallet,
    props.candyMachineId,
    props.connection,
    refreshCandyMachineState,
  ]);
  
  return (
    <div>
    <div className="header-container">
      <img className="logo logo-right" src="img/logo-shadow.png"/>
      <div className="header-pad"></div>
      <div className="header-item">
        <a href="https://medium.com/@genenft"><img className="social" src="img/medium-icon.png"/></a>
      </div>
      <div className="header-item">
        <a href="https://discord.gg/Ppa2NkmKet"><img className="social" src="img/discord.svg"/></a>
      </div>
      <div className="header-item">
        <a href="https://twitter.com/genenft"><img className="social" src="img/twitter.svg"/></a>
      </div>
    </div>
    <div className="section-header hero">
      <div className="sh-topline"></div>
      <div className="sh-text">the science of minting</div>
      <div className="sh-bottomline" style={{"width": "22.1em","borderRightWidth":"12.0em"}}></div>
    </div>
    <div className="front-para">
      <div>
        <ul>
          <li>Our full site is at <a href="https://genenft.art">genenft.art</a>.</li>
          <li>Always use a <a href="https://www.patlid.com/posts/buy-solana-nfts-best-practice-safety-security-checklist">
            burner wallet</a> when minting.</li>
          <li>Check you are on the correct site URL. URLs in DMs are always scams.</li>
        </ul>
      </div>
    </div>
    <div className="section-header sub-header">
      <div className="sh-topline"></div>
      <div className="sh-text">Pre-release of chromosome 22</div>
      <div className="sh-bottomline" style={{"width": "25.0em", "borderRightWidth": "2.2em"}}></div>
    </div>
    <div className="front-para"><div>You're lucky enough to have found our reduced price pre-release! We're minting
      all the genes on <a href="https://en.wikipedia.org/wiki/Chromosome_22">chromosome 22</a>, which was the first to be fully sequenced in 1999.
    For each mint you'll receive one unique NFT artwork from chromosome 22 of the HUMAN collection at random.</div>
      <div>Price: 0.2 SOL + ~0.01 SOL fees</div>
    </div>
    <div>
      <div style={{"display":"flex", "justifyContent":"center", "gap":"2em"}}>
          {!wallet.connected ? (
            <ConnectButton style={{backgroundColor:"#1cc964"}}>Connect Wallet</ConnectButton>
          ) : (
            <>

              <Header candyMachine={candyMachine} />
              <MintContainer>
                {candyMachine?.state.isActive &&
                candyMachine?.state.gatekeeper &&
                wallet.publicKey &&
                wallet.signTransaction ? (
                  <GatewayProvider
                    wallet={{
                      publicKey:
                        wallet.publicKey ||
                        new PublicKey(CANDY_MACHINE_PROGRAM),
                      //@ts-ignore
                      signTransaction: wallet.signTransaction,
                    }}
                    gatekeeperNetwork={
                      candyMachine?.state?.gatekeeper?.gatekeeperNetwork
                    }
                    clusterUrl={rpcUrl}
                    options={{ autoShowModal: false }}
                  >
                    <MintButton
                      candyMachine={candyMachine}
                      isMinting={isUserMinting}
                      onMint={onMint}
                    />
                  </GatewayProvider>
                ) : (
                  <>
                  <MintButton
                    candyMachine={candyMachine}
                    isMinting={isUserMinting}
                    onMint={onMint}
                  />
                  </>
                )}
              </MintContainer>
            </>
          )}
      </div>
      <div >
        {(candyMachine ? toDate(candyMachine?.state.goLiveDate) : props.startDate)! > Date.now() ?
        <Countdown
        date={candyMachine ? toDate(candyMachine?.state.goLiveDate) : props.startDate}
        // onMount={({completed}) => completed && setIsActive(true)}
        onComplete={() => (refreshCandyMachineState())}
        renderer={renderCounter}
      /> : null }
    <div className="front-para" style={{paddingTop:"3em"}}><div>After mint, come join us in the discord or on twitter to find out about your gene:
      </div></div>
      <div className="button-container">
                <a className="green-button" href="https://discord.gg/Ppa2NkmKet">
                    <img src="img/discord.svg" className='discord-button-img'/>
                    Discover your gene
                </a>
      </div>
    </div>
      <Snackbar
        open={alertState.open}
        autoHideDuration={6000}
        onClose={() => setAlertState({ ...alertState, open: false })}
      >
        <Alert
          onClose={() => setAlertState({ ...alertState, open: false })}
          severity={alertState.severity}
        >
          {alertState.message}
        </Alert>
      </Snackbar>
      
    </div>
    
    </div>
  );
};

export default Home;
